.skill-section {
    padding: 20px;
    width: 92vw;
    margin: auto;
    background: rgba(155, 180, 197, 0.1);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    height: auto;
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
  }
  
  .skill-section h2 {
    margin-top: 0;
    text-align: center;
  }
  
  .skill-category {
    margin-bottom: 5rem;
  }
  
  .skill-category h3 {
    margin-bottom: 0px;
    margin-top: 2px;
    height: 1px;
    width: 100%;
  }
  
  .skill-category ul {
    list-style: circle;
    padding: 10px;
    /* margin-top: 1px; */
    /* margin-bottom: ; */
    height: 0px;
    width: 100vw;
  }
  
  .skill-category li {
    padding: 0rem 8rem;
    border-bottom: 1px solid #ddd;
    display: flex;
    flex-direction: column;
    width: 100%;
    /* margin: 1rem; */
    height: 30px;

  }
  
  .skill-category li:last-child {
    border-bottom: none;
  }
  